import React, { Component } from 'react';
import { Image, Container, Grid } from 'semantic-ui-react';
import _ from 'lodash';
import { client } from '../../globals';

class Perks extends Component {
  state = { perks: [] };

  componentDidMount = () => {
    client.getEntries({ content_type: 'perk', order: 'fields.title' }).then(response => {
      this.setState({ perks: response.items });
    });
  };

  render() {
    return (
      <div className="half-section white-background">
        <Container className="quarter-section" textAlign="center">
          <h2>{this.props.title}</h2>
          {this.props.subtitle}
          <Grid columns={this.props.gridSize} className="quarter-section">
            {_.map(_.chunk(this.state.perks, this.props.gridSize), row => (
              <Grid.Row centered className="quarter-section">
                {_.map(row, perk => (
                  <Grid.Column textAlign="center">
                    <Image
                      src={`https:${perk.fields.icon.fields.file.url}`}
                      className="display-inline-block"
                      height={65}
                      title={perk.fields.caption}
                    />
                    <h3 className="text-center">{perk.fields.title}</h3>
                    <p>{perk.fields.description}</p>
                  </Grid.Column>
                ))}
              </Grid.Row>
            ))}
          </Grid>
        </Container>
        {
          // <Button content="See customer stories" as="a" />
        }
      </div>
    );
  }
}

export default Perks;
