import React, { Component } from 'react';
import { Ref } from 'semantic-ui-react';
import _ from 'lodash';
import Topbar from '../components/Topbar';
import Navbar from '../components/Navbar';
import Footer from '../components/Footer';
import FrontBanner from '../components/Home/FrontBanner';
import { client, contentRef } from '../globals';

class Pricing extends Component {
  state = {
    title: '',
    subtitle: '',
    titleBannerImage: {},
    titleBannerTintColor: '',
  };

  componentDidMount = () => {
    client.getEntry('6YaPUlSAq1X3OZPI6DvIFl').then(response =>
      this.setState({
        title: response.fields.title,
        subtitle: response.fields.subtitle,
        titleBannerImage: response.fields.titleBannerImage,
        titleBannerTintColor: response.fields.titleBannerTintColor,
      }),
    );
  };

  render() {
    return (
      !_.isEmpty(this.state.title) && (
        <>
          <Topbar />
          <Ref innerRef={contentRef}>
            <div>
              <Navbar contextRef={contentRef} />
              <FrontBanner
                title={this.state.title}
                subtitle={this.state.subtitle}
                image={this.state.titleBannerImage}
                tint={this.state.titleBannerTintColor}
              />
              <Footer />
            </div>
          </Ref>
        </>
      )
    );
  }
}

export default Pricing;
