import React from 'react';
import { Container, Button, Icon } from 'semantic-ui-react';

const MobileApp = ({ title, subtitle, appleLink, googlePlayLink, image, tint }) => (
  <div
    className="darker-background"
    id="mobile-app"
    style={{ backgroundColor: tint, backgroundImage: `url(https:${image.fields.file.url})` }}
  >
    <Container textAlign="center" centered="true" fluid>
      <h1>{title}</h1>
      <h4>{subtitle}</h4>
      <Button size="large" href={googlePlayLink} disabled>
        <Icon name="google play" />
        Google Play
      </Button>
      <Button size="large" href={appleLink} disabled>
        <Icon name="apple" />
        App Store
      </Button>
    </Container>
  </div>
);

export default MobileApp;
