import React, { Component } from 'react';
import RichTextToReact from 'rich-text-to-react';
import { Ref, Container, Image, Header } from 'semantic-ui-react';
import { withRouter } from 'react-router-dom';
import _ from 'lodash';
import moment from 'moment';
import Topbar from '../components/Topbar';
import Navbar from '../components/Navbar';
import Footer from '../components/Footer';
import { client, contentRef } from '../globals';

class Post extends Component {
  state = { data: {} };

  componentDidMount = () => {
    const slug = this.props.match.params.slug;
    client
      .getEntries({ 'fields.slug': slug, content_type: 'post' })
      .then(response => this.setState({ data: response.items[0] }));
  };

  render() {
    return (
      !_.isEmpty(this.state.data) && (
        <>
          <Topbar />
          <Ref innerRef={contentRef}>
            <div>
              <Navbar contextRef={this.contentRef} />
              <Container className="quarter-section">
                <Header as="h1">
                  {this.state.data.fields.title}
                  <Header.Subheader>
                    by {this.state.data.fields.title}. Published on{' '}
                    {moment(this.state.data.fields.datePublished).format('MMMM DD YYYY')}.
                  </Header.Subheader>
                </Header>
                {!_.isNil(this.state.data.fields.featuredImage) ? (
                  <>
                    <Image
                      src={`https:${this.state.data.fields.featuredImage.fields.file.url}`}
                      className="quarter-section"
                      wrapped
                      ui={false}
                    />
                  </>
                ) : null}
                <RichTextToReact document={this.state.data.fields.body} />
              </Container>
              <Footer />
            </div>
          </Ref>
        </>
      )
    );
  }
}

export default withRouter(Post);
