import React, { Component } from 'react';
import { Ref, Responsive } from 'semantic-ui-react';
import _ from 'lodash';
import Topbar from '../components/Topbar';
import Navbar from '../components/Navbar';
import Footer from '../components/Footer';
import FrontBanner from '../components/Home/FrontBanner';
// import SectionNavigation from '../components/Home/SectionNavigation';
// import ShortDescription from '../components/Home/ShortDescription';
import NotableFeatures from '../components/Home/NotableFeatures';
import SectionThree from '../components/Home/SectionThree';
import Perks from '../components/Home/Perks';
// import Background from '../components/Home/Background';
// import Endorsements from '../components/Home/Endorsements';
// import Pricing from '../components/Home/Pricing';
import MobileApp from '../components/Home/MobileApp';
import RecentPosts from '../components/Home/RecentPosts';
import DemoForm from '../components/DemoForm';
import { client, contentRef, splitElementIntoAttributes } from '../globals';

class Home extends Component {
  state = {
    heroBannerTitle: '',
    heroBannerSubtitle: '',
    heroBannerButtons: [],
    heroBannerTintColor: '',
    featuresTitle: '',
    featuresSubtitle: '',
    sectionThreeTitle: '',
    sectionThreeSubtitle: '',
    sectionThreeImage: {},
    sectionThreeTintColor: '',
    sectionThreeButtons: [],
    perksTitle: '',
    perksSubtitle: '',
    mobileSectionTitle: '',
    mobileSectionSubtitle: '',
    mobileSectionTintColor: '',
    appleStoreLink: '',
    googlePlayLink: '',
  };

  componentDidMount = () => {
    client.getEntry('6t3ob1JG0uaoZRi3dhT6Jp').then(response =>
      this.setState({
        heroBannerTitle: response.fields.heroBannerTitle,
        heroBannerSubtitle: response.fields.heroBannerSubtitle,
        heroBannerButtons: response.fields.heroBannerButtons,
        heroBannerTintColor: response.fields.heroBannerTintColor,
        heroBannerImage: response.fields.heroBannerImage,
        featuresTitle: response.fields.featuresTitle,
        featuresSubtitle: response.fields.featuresSubtitle,
        sectionThreeTitle: response.fields.sectionThreeTitle,
        sectionThreeSubtitle: response.fields.sectionThreeSubtitle,
        sectionThreeImage: response.fields.sectionThreeImage,
        sectionThreeTintColor: response.fields.sectionThreeTintColor,
        sectionThreeButtons: response.fields.sectionThreeButtons,
        perksTitle: response.fields.perksTitle,
        perksSubtitle: response.fields.perksSubtitle,
        mobileSectionTitle: response.fields.mobileSectionTitle,
        mobileSectionSubtitle: response.fields.mobileSectionSubtitle,
        mobileSectionTintColor: response.fields.mobileSectionTintColor,
        appleStoreLink: response.fields.appleStoreLink,
        googlePlayLink: response.fields.googlePlayLink,
        mobileSectionImage: response.fields.mobileSectionImage,
        recentPostsSectionTitle: response.fields.recentPostsSectionTitle,
        recentPostsSectionSubtitle: response.fields.recentPostsSectionSubtitle,
      }),
    );
  };

  render() {
    return (
      !_.isEmpty(this.state.heroBannerTitle) && (
        <>
          {
            // <Background />
          }
          <Topbar />
          <Ref innerRef={contentRef}>
            <div>
              <Navbar contextRef={contentRef} />
              <FrontBanner
                title={this.state.heroBannerTitle}
                subtitle={this.state.heroBannerSubtitle}
                buttons={_.map(this.state.heroBannerButtons, element => {
                  let attributes = splitElementIntoAttributes(element);
                  return { content: attributes[0], color: attributes[1], href: attributes[2] };
                })}
                tint={this.state.heroBannerTintColor}
                image={this.state.heroBannerImage}
              />
              {
                // <SectionNavigation
                //   names={_.map(this.filterElementsBetweenHr(1, 2), element => {
                //     return { name: element.props.children[0] };
                //   })}
                // />
              }
              <Responsive maxWidth={485}>
                <NotableFeatures title={this.state.featuresTitle} subtitle={this.state.featuresSubtitle} gridSize={1} />
              </Responsive>
              <Responsive minWidth={486} maxWidth={768}>
                <NotableFeatures title={this.state.featuresTitle} subtitle={this.state.featuresSubtitle} gridSize={2} />
              </Responsive>
              <Responsive minWidth={769}>
                <NotableFeatures title={this.state.featuresTitle} subtitle={this.state.featuresSubtitle} gridSize={3} />
              </Responsive>
              <SectionThree
                title={this.state.sectionThreeTitle}
                subtitle={this.state.sectionThreeSubtitle}
                image={this.state.sectionThreeImage}
                tint={this.state.sectionThreeTintColor}
                buttons={_.map(this.state.sectionThreeButtons, element => {
                  let attributes = splitElementIntoAttributes(element);
                  return { content: attributes[0], color: attributes[1], href: attributes[2] };
                })}
              />
              <Responsive maxWidth={485}>
                <Perks title={this.state.perksTitle} subtitle={this.state.perksSubtitle} gridSize={1} />
                <DemoForm size={1} />
                <MobileApp
                  title={this.state.mobileSectionTitle}
                  subtitle={this.state.mobileSectionSubtitle}
                  appleLink={this.state.appleStoreLink}
                  googlePlayLink={this.state.googlePlayLink}
                  image={this.state.mobileSectionImage}
                  tint={this.state.mobileSectionTintColor}
                />
                <RecentPosts
                  title={this.state.recentPostsSectionTitle}
                  subtitle={this.state.recentPostsSectionSubtitle}
                  gridSize={1}
                />
              </Responsive>
              <Responsive minWidth={486} maxWidth={768}>
                <Perks title={this.state.perksTitle} subtitle={this.state.perksSubtitle} gridSize={2} />
                <DemoForm size={2} />
                <MobileApp
                  title={this.state.mobileSectionTitle}
                  subtitle={this.state.mobileSectionSubtitle}
                  appleLink={this.state.appleStoreLink}
                  googlePlayLink={this.state.googlePlayLink}
                  image={this.state.mobileSectionImage}
                  tint={this.state.mobileSectionTintColor}
                />
                <RecentPosts
                  title={this.state.recentPostsSectionTitle}
                  subtitle={this.state.recentPostsSectionSubtitle}
                  gridSize={2}
                />
              </Responsive>
              <Responsive minWidth={769}>
                <Perks title={this.state.perksTitle} subtitle={this.state.perksSubtitle} gridSize={5} />
                <DemoForm size={2} />
                <MobileApp
                  title={this.state.mobileSectionTitle}
                  subtitle={this.state.mobileSectionSubtitle}
                  appleLink={this.state.appleStoreLink}
                  googlePlayLink={this.state.googlePlayLink}
                  image={this.state.mobileSectionImage}
                  tint={this.state.mobileSectionTintColor}
                />
                <RecentPosts
                  title={this.state.recentPostsSectionTitle}
                  subtitle={this.state.recentPostsSectionSubtitle}
                  gridSize={3}
                />
              </Responsive>
              {
                // <Pricing
                //   title={this.state.h1[4]}
                //   subtitle={this.state.data[parseInt(this.state.h1[4].key, 10) + 1]}
                //   plans={_.map(this.filterElementsBetweenHr(9, 10), element => {
                //     let attributes = splitElementIntoAttributes(element);
                //     return { title: attributes[0], content: attributes[1], meta: attributes[2] };
                //   })}
                // />
                // <Endorsements title={_.first(this.state.h3[0].props.children)} />
              }
              <Footer />
            </div>
          </Ref>
        </>
      )
    );
  }
}

export default Home;
