import React, { Component } from 'react';
import { Grid, Card, Form, Input, Select, Button, Message } from 'semantic-ui-react';
import _ from 'lodash';
import axios from 'axios';
import { documentToReactComponents } from '@contentful/rich-text-react-renderer';
import { client } from '../globals';

const HUBSPOT_URL = 'https://api.hsforms.com/submissions/v3/integration/submit/6456375';

class DemoForm extends Component {
  state = {
    email: '',
    disabled: false,
    loading: false,
    done: false,
    success: false,
    message: '',
    openBeta: {
      fullName: '',
      ofEmployees: '',
      company: '',
      phone: '',
      signupSource: '',
    },
  };

  snakedKeys = obj => _.mapKeys(obj, (val, key) => _.snakeCase(key));

  fieldChange = (fieldName, value) => this.setState({ [fieldName]: value });

  handleSubmit = async e => {
    e.preventDefault();

    const { email, disabled, openBeta } = this.state;

    if (disabled) return;

    this.setState({ disabled: true, loading: true, done: false });
    await this.openBetaSubmit(this.snakedKeys({ ...openBeta, email }));

    this.setState({ disabled: false, loading: false });
  };

  openBetaChange = (fieldName, value) => {
    const { openBeta } = this.state;

    this.setState({ openBeta: { ...openBeta, [fieldName]: value } });
  };

  openBetaSubmit = async formFields => {
    const url = `${HUBSPOT_URL}/d246d058-1b85-4614-aa4c-651640567c20`;

    const fields = _.map(formFields, (val, key) => ({
      name: key,
      value: val,
    }));

    try {
      await axios.post(url, { fields });
      const name = this.state.openBeta.fullName;
      this.setState({
        success: true,
        done: true,
        message: `Thank you, ${name}! Your log in credentials are on the way. One of our payroll experts will get in
        touch with you shortly to help you set up.`,
      });
    } catch (error) {
      this.setState({ success: false, done: true, message: 'An error has occured. Try again later.' });
    }
  };

  componentDidMount = () => {
    client.getEntry('7jOgwdYGqxXBOHhKUKsRzx').then(response =>
      this.setState({
        formTitle: response.fields.formTitle,
        formSubtitle: response.fields.formSubtitle,
        additionalInformationTitle: response.fields.additionalInformationTitle,
        additionalInformationContent: documentToReactComponents(response.fields.additionalInformationContent),
      }),
    );
  };

  render() {
    return (
      <Grid
        className={this.props.dark ? 'margin-auto section tk-dark-blue' : 'margin-auto section tk-light-blue'}
        id="get-started"
        style={{ marginLeft: 'auto', marginRight: 'auto' }}
      >
        <Grid.Row columns={this.props.size}>
          <Grid.Column className="demo-form-left" textAlign="center">
            <h2>{this.state.additionalInformationTitle}</h2>
            <div className="wider-line-height">{this.state.additionalInformationContent}</div>
          </Grid.Column>
          <Grid.Column>
            <Card className="demo-form borderless tk-light-blue" fluid>
              <Card.Content textAlign="center">
                <Message positive={this.state.success} negative={!this.state.success} hidden={!this.state.done}>
                  {this.state.message}
                </Message>
                <Card.Header className="white-text">
                  {this.props.bigTitle ? (
                    <>
                      <h1>{this.state.formTitle}</h1>
                    </>
                  ) : (
                    this.state.formTitle
                  )}
                </Card.Header>
                <Card.Description className="margin-bottom-small white-text">
                  {this.state.formSubtitle}
                </Card.Description>
                <Form onSubmit={this.handleSubmit} loading={!!this.state.loading}>
                  <Form.Field disabled={this.state.done}>
                    <Input
                      id="full_name"
                      placeholder="Full Name"
                      onChange={(e, { value }) => this.openBetaChange('fullName', value)}
                    />
                  </Form.Field>
                  <Form.Field disabled={this.state.done}>
                    <Input
                      id="number_of_employees"
                      placeholder="Number of Employees"
                      onChange={(e, { value }) => this.openBetaChange('ofEmployees', value)}
                    />
                  </Form.Field>
                  <Form.Field disabled={this.state.done}>
                    <Input
                      id="company_name"
                      placeholder="Company Name"
                      onChange={(e, { value }) => this.openBetaChange('company', value)}
                    />
                  </Form.Field>
                  <Form.Field disabled={this.state.done}>
                    <Input
                      id="email"
                      placeholder="Email address"
                      onChange={(e, { value }) => this.fieldChange('email', value)}
                    />
                  </Form.Field>
                  <Form.Field disabled={this.state.done}>
                    <Input
                      id="phone"
                      placeholder="Phone Number"
                      onChange={(e, { value }) => this.openBetaChange('phone', value)}
                    />
                  </Form.Field>
                  <Form.Field
                    control={Select}
                    options={[
                      { key: 'social_media', text: 'Social Media', value: 'social_media' },
                      { key: 'search_engine', text: 'Search Engines', value: 'search_engine' },
                      { key: 'roadshow', text: 'Roadshow', value: 'roadshow' },
                      { key: 'email_newsletter', text: 'Email Newsletter', value: 'email_newsletter' },
                      { key: 'referral', text: 'Referral', value: 'referral' },
                      { key: 'others', text: 'Others', value: 'others' },
                    ]}
                    placeholder="Where did you find us?"
                    onChange={(e, { value }) => this.openBetaChange('signupSource', value)}
                    disabled={this.state.done}
                  />
                  <Button type="submit">Submit</Button>
                </Form>
              </Card.Content>
            </Card>
          </Grid.Column>
        </Grid.Row>
      </Grid>
    );
  }
}

export default DemoForm;
