import React, { Component } from 'react';
import RichTextToReact from 'rich-text-to-react';
import _ from 'lodash';
import { Container, Grid, Card, Image, Button } from 'semantic-ui-react';
import { client } from '../../globals';

class RecentPosts extends Component {
  state = { posts: [] };

  componentDidMount = () => {
    client.getEntries({ content_type: 'post', limit: 3, order: 'fields.datePublished' }).then(response =>
      this.setState({
        posts: response.items,
      }),
    );
  };

  render() {
    return (
      !_.isEmpty(this.state.posts) && (
        <div className="half-section white-background" id="blog-posts">
          <Container textAlign="center">
            <h2>{this.props.title}</h2>
            {this.props.subtitle}
          </Container>
          <Container>
            <Grid columns={this.props.gridSize} className="quarter-section" centered>
              {_.map(_.chunk(this.state.posts, this.props.gridSize), row => (
                <Card.Group className="quarter-section" stackable>
                  {_.map(row, post => (
                    <Card centered raised style={{ margin: '1rem' }}>
                      {!_.isNil(post.fields.featuredImage) ? (
                        <>
                          <Image src={`https:${post.fields.featuredImage.fields.file.url}`} wrapped ui={false} />
                        </>
                      ) : null}
                      <Card.Content>
                        <Card.Header>{post.fields.title}</Card.Header>
                        <Card.Meta>by {post.fields.author}</Card.Meta>
                        <Card.Description>
                          <RichTextToReact document={post.fields.openingParagraph} />
                        </Card.Description>
                      </Card.Content>
                      <Card.Content extra textAlign="center">
                        <Button as="a" content="Read more" className="tk-blue" href={`resources/${post.fields.slug}`} />
                      </Card.Content>
                    </Card>
                  ))}
                </Card.Group>
              ))}
            </Grid>
          </Container>
          <Container textAlign="center">
            <Button content="See all posts" as="a" href="/resources" />
          </Container>
        </div>
      )
    );
  }
}

export default RecentPosts;
