import React, { Component } from 'react';
import RichTextToReact from 'rich-text-to-react';
import { Ref, Container, Grid, Card, Image, Button, Pagination } from 'semantic-ui-react';
import _ from 'lodash';
import moment from 'moment';
import Topbar from '../components/Topbar';
import Navbar from '../components/Navbar';
import Footer from '../components/Footer';
import { client, contentRef } from '../globals';

class Resources extends Component {
  state = {
    title: '',
    subtitle: '',
    postsPerPage: 0,
    posts: [],
    length: 0,
  };

  componentDidMount = () => {
    client
      .getEntry('i5OGdxXQaPmntRbGTDVK1')
      .then(response =>
        this.setState({
          title: response.fields.title,
          subtitle: response.fields.subtitle,
          postsPerPage: response.fields.postsPerPage,
        }),
      )
      .then(() => {
        client.getEntries({ content_type: 'post' }).then(response => this.setState({ length: response.items.length }));

        this.loadPage(1);
      });
  };

  loadPage = activePage => {
    client
      .getEntries({
        content_type: 'post',
        skip: (activePage - 1) * this.state.postsPerPage,
        limit: this.state.postsPerPage,
        order: '-fields.datePublished',
      })
      .then(response => this.setState({ posts: response.items }));
  };

  render() {
    return (
      !_.isEmpty(this.state.title) && (
        <>
          <Topbar />
          <Ref innerRef={contentRef}>
            <div>
              <Navbar contextRef={contentRef} />
              <Container className="quarter-section" centered="true">
                <h1 className="text-center">{this.state.title}</h1>
                <h3 className="text-center">{this.state.subtitle}</h3>
                {_.map(this.state.posts, post => (
                  <Card centered raised fluid>
                    <Card.Content>
                      <Grid>
                        <Grid.Column computer={4} tablet={4} mobile={16}>
                          {!_.isNil(post.fields.featuredImage) ? (
                            <>
                              <Image src={`https:${post.fields.featuredImage.fields.file.url}`} wrapped ui={false} />
                            </>
                          ) : null}
                        </Grid.Column>
                        <Grid.Column computer={8} tablet={8} mobile={16}>
                          <h3>{post.fields.title}</h3>
                          <Card.Meta>
                            by {post.fields.author}. Published on{' '}
                            {moment(post.fields.datePublished).format('MMMM DD YYYY')}.
                          </Card.Meta>
                          <Card.Description>
                            <RichTextToReact document={post.fields.openingParagraph} />
                          </Card.Description>
                        </Grid.Column>
                      </Grid>
                    </Card.Content>
                    <Card.Content extra textAlign="center">
                      <Button as="a" content="Read more" className="tk-blue" href={`resources/${post.fields.slug}`} />
                    </Card.Content>
                  </Card>
                ))}
                <Container textAlign="center">
                  <Pagination
                    defaultActivePage={1}
                    totalPages={_.ceil(this.state.length / this.state.postsPerPage)}
                    onPageChange={(e, { activePage }) => this.loadPage(activePage)}
                  />
                </Container>
              </Container>
              <Footer />
            </div>
          </Ref>
        </>
      )
    );
  }
}

export default Resources;
