import React, { Component } from 'react';
import _ from 'lodash';
import { Container, Grid, Image } from 'semantic-ui-react';
import { client } from '../../globals';

class NotableFeatures extends Component {
  state = { features: [] };

  componentDidMount = () => {
    client.getEntries({ content_type: 'feature', order: 'fields.title' }).then(response => {
      this.setState({ features: response.items });
    });
  };

  render() {
    return (
      <div className="half-section white-background text-center" id="notable-features">
        <Container className="quarter-section" textAlign="center">
          <h2>{this.props.title}</h2>
          {this.props.subtitle}
          <Grid columns={this.props.gridSize} className="quarter-section">
            {_.map(_.chunk(this.state.features, this.props.gridSize), row => (
              <Grid.Row centered className="quarter-section">
                {_.map(row, feature => (
                  <Grid.Column textAlign="center">
                    <Image
                      src={`https:${feature.fields.icon.fields.file.url}`}
                      className="display-inline-block"
                      height={65}
                      title={feature.fields.caption}
                    />
                    <h3 className="text-center">{feature.fields.title}</h3>
                    <p>{feature.fields.description}</p>
                  </Grid.Column>
                ))}
              </Grid.Row>
            ))}
          </Grid>
        </Container>
        {
          // <Button content="Explore more features" as="a" href="/features" />
        }
      </div>
    );
  }
}

export default NotableFeatures;
