import React from 'react';
import _ from 'lodash';
import { Container, Button } from 'semantic-ui-react';

const FrontBanner = ({ title, subtitle, buttons, tint, image }) => {
  return (
    <div className="intro" style={{ backgroundColor: tint, backgroundImage: `url(https:${image.fields.file.url})` }}>
      <Container className="overlay-text" id="intro-banner">
        <h1>{title}</h1>
        <h3>{subtitle}</h3>
        {_.map(buttons, button => (
          <>
            <Button content={button.content} as={button.as} className={button.color} href={button.href} />
          </>
        ))}
      </Container>
    </div>
  );
};

export default FrontBanner;
