import React from 'react';
import _ from 'lodash';
import { Button, Container } from 'semantic-ui-react';

const SectionThree = ({ title, subtitle, image, tint, buttons }) => (
  <div id="section-three" style={{ backgroundColor: tint, backgroundImage: `url(https:${image.fields.file.url})` }}>
    <Container textAlign="center" className="white-text" centered="true">
      <h1>{title}</h1>
      <p>{subtitle}</p>
      {_.map(buttons, button => (
        <>
          <Button content={button.content} as={button.as} className={button.color} href={button.href} />
        </>
      ))}
    </Container>
  </div>
);

export default SectionThree;
